"use client";

import React, { useEffect, useRef } from "react";
import { Camera, Mesh, Plane, Program, Renderer, Texture, Transform } from "ogl";
import styles from "./CircularGallery.module.css";

function debounce<T extends (...args: any[]) => void>(func: T, wait: number) {
  let timeout: ReturnType<typeof setTimeout> | undefined;
  return function (this: any, ...args: Parameters<T>) {
    if (timeout) clearTimeout(timeout);
    timeout = setTimeout(() => func.apply(this, args), wait);
  } as T;
}

function lerp(p1: number, p2: number, t: number) {
  return p1 + (p2 - p1) * t;
}

function autoBind(instance: any) {
  const proto = Object.getPrototypeOf(instance);
  Object.getOwnPropertyNames(proto).forEach((key) => {
    if (key !== "constructor" && typeof instance[key] === "function") {
      instance[key] = instance[key].bind(instance);
    }
  });
}

function createTextTexture(gl: WebGLRenderingContext, text: string, font = "bold 30px monospace", color = "black") {
  const canvas = document.createElement("canvas");
  const context = canvas.getContext("2d")!;
  context.font = font;
  const metrics = context.measureText(text);
  const textWidth = Math.ceil(metrics.width);
  const textHeight = Math.ceil(parseInt(font, 10) * 1.2);
  canvas.width = textWidth + 20;
  canvas.height = textHeight + 20;
  context.font = font;
  context.fillStyle = color;
  context.textBaseline = "middle";
  context.textAlign = "center";
  context.clearRect(0, 0, canvas.width, canvas.height);
  context.fillText(text, canvas.width / 2, canvas.height / 2);
  const texture = new Texture(gl as any, { generateMipmaps: false });
  texture.image = canvas as any;
  return { texture, width: canvas.width, height: canvas.height };
}

class Title {
  gl: WebGLRenderingContext;
  plane: Mesh;
  renderer: Renderer;
  text: string;
  textColor: string;
  font: string;
  mesh!: Mesh;

  constructor({ gl, plane, renderer, text, textColor = "#545050", font = "30px sans-serif" }: any) {
    autoBind(this);
    this.gl = gl;
    this.plane = plane;
    this.renderer = renderer;
    this.text = text;
    this.textColor = textColor;
    this.font = font;
    this.createMesh();
  }
  createMesh() {
    const { texture, width, height } = createTextTexture(this.gl, this.text, this.font, this.textColor);
    const geometry = new Plane(this.gl as any);
    const program = new Program(this.gl as any, {
      vertex: `
        attribute vec3 position;
        attribute vec2 uv;
        uniform mat4 modelViewMatrix;
        uniform mat4 projectionMatrix;
        varying vec2 vUv;
        void main() {
          vUv = uv;
          gl_Position = projectionMatrix * modelViewMatrix * vec4(position, 1.0);
        }
      `,
      fragment: `
        precision highp float;
        uniform sampler2D tMap;
        varying vec2 vUv;
        void main() {
          vec4 color = texture2D(tMap, vUv);
          if (color.a < 0.1) discard;
          gl_FragColor = color;
        }
      `,
      uniforms: { tMap: { value: texture } },
      transparent: true,
    });
    this.mesh = new Mesh(this.gl as any, { geometry, program });
    const aspect = width / height;
    const textHeight = (this.plane as any).scale.y * 0.15;
    const textWidth = textHeight * aspect;
    (this.mesh as any).scale.set(textWidth, textHeight, 1);
    (this.mesh as any).position.y = -(this.plane as any).scale.y * 0.5 - textHeight * 0.5 - 0.05;
    (this.mesh as any).setParent(this.plane);
  }
}

class Media {
  extra = 0;
  geometry: Plane;
  gl: WebGLRenderingContext;
  image: string;
  index: number;
  length: number;
  renderer: Renderer;
  scene: Transform;
  screen: { width: number; height: number };
  text: string;
  viewport: { width: number; height: number };
  bend: number;
  textColor: string;
  borderRadius: number;
  font: string;
  program!: Program;
  plane!: Mesh & { program: any };
  title!: Title;
  widthTotal!: number;
  x!: number;
  scale!: number;
  width!: number;
  padding!: number;
  isBefore?: boolean;
  isAfter?: boolean;
  speed?: number;

  constructor({
    geometry,
    gl,
    image,
    index,
    length,
    renderer,
    scene,
    screen,
    text,
    viewport,
    bend,
    textColor,
    borderRadius = 0,
    font,
  }: any) {
    this.geometry = geometry as any;
    this.gl = gl;
    this.image = image;
    this.index = index;
    this.length = length;
    this.renderer = renderer;
    this.scene = scene;
    this.screen = screen;
    this.text = text;
    this.viewport = viewport;
    this.bend = bend;
    this.textColor = textColor;
    this.borderRadius = borderRadius;
    this.font = font;
    this.createShader();
    this.createMesh();
    this.createTitle();
    this.onResize({});
  }
  createShader() {
    const texture = new Texture(this.gl as any, {
      generateMipmaps: true,
    });
    this.program = new Program(this.gl as any, {
      depthTest: false,
      depthWrite: false,
      vertex: `
        precision highp float;
        attribute vec3 position;
        attribute vec2 uv;
        uniform mat4 modelViewMatrix;
        uniform mat4 projectionMatrix;
        uniform float uTime;
        uniform float uSpeed;
        varying vec2 vUv;
        void main() {
          vUv = uv;
          vec3 p = position;
          p.z = (sin(p.x * 4.0 + uTime) * 1.5 + cos(p.y * 2.0 + uTime) * 1.5) * (0.1 + uSpeed * 0.5);
          gl_Position = projectionMatrix * modelViewMatrix * vec4(p, 1.0);
        }
      `,
      fragment: `
        precision highp float;
        uniform vec2 uImageSizes;
        uniform vec2 uPlaneSizes;
        uniform sampler2D tMap;
        uniform float uBorderRadius;
        varying vec2 vUv;
        
        float roundedBoxSDF(vec2 p, vec2 b, float r) {
          vec2 d = abs(p) - b;
          return length(max(d, vec2(0.0))) + min(max(d.x, d.y), 0.0) - r;
        }
        
        void main() {
          vec2 ratio = vec2(
            min((uPlaneSizes.x / uPlaneSizes.y) / (uImageSizes.x / uImageSizes.y), 1.0),
            min((uPlaneSizes.y / uPlaneSizes.x) / (uImageSizes.y / uImageSizes.x), 1.0)
          );
          vec2 uv = vec2(
            vUv.x * ratio.x + (1.0 - ratio.x) * 0.5,
            vUv.y * ratio.y + (1.0 - ratio.y) * 0.5
          );
          vec4 color = texture2D(tMap, uv);
          
          float d = roundedBoxSDF(vUv - 0.5, vec2(0.5 - uBorderRadius), uBorderRadius);
          
          float edgeSmooth = 0.002;
          float alpha = 1.0 - smoothstep(-edgeSmooth, edgeSmooth, d);
          
          gl_FragColor = vec4(color.rgb, alpha);
        }
      `,
      uniforms: {
        tMap: { value: texture },
        uPlaneSizes: { value: [0, 0] },
        uImageSizes: { value: [0, 0] },
        uSpeed: { value: 0 },
        uTime: { value: 100 * Math.random() },
        uBorderRadius: { value: this.borderRadius },
      },
      transparent: true,
    });
    const img = new Image();
    (img as any).crossOrigin = "anonymous";
    img.src = this.image;
    img.onload = () => {
      (texture as any).image = img;
      (this.program as any).uniforms.uImageSizes.value = [
        (img as HTMLImageElement).naturalWidth,
        (img as HTMLImageElement).naturalHeight,
      ];
    };
  }
  createMesh() {
    this.plane = new Mesh(this.gl as any, {
      geometry: this.geometry as any,
      program: this.program as any,
    }) as any;
    (this.plane as any).setParent(this.scene);
  }
  createTitle() {
    this.title = new Title({
      gl: this.gl,
      plane: this.plane,
      renderer: this.renderer,
      text: this.text,
      textColor: this.textColor,
      fontFamily: this.font,
    });
  }
  update(scroll: { current: number; last: number }, direction: "left" | "right") {
    (this.plane as any).position.x = this.x - scroll.current - this.extra;

    const x = (this.plane as any).position.x;
    const H = this.viewport.width / 2;

    if (this.bend === 0) {
      (this.plane as any).position.y = 0;
      (this.plane as any).rotation.z = 0;
    } else {
      const B_abs = Math.abs(this.bend);
      const R = (H * H + B_abs * B_abs) / (2 * B_abs);
      const effectiveX = Math.min(Math.abs(x), H);

      const arc = R - Math.sqrt(R * R - effectiveX * effectiveX);
      if (this.bend > 0) {
        (this.plane as any).position.y = -arc;
        (this.plane as any).rotation.z = -Math.sign(x) * Math.asin(effectiveX / R);
      } else {
        (this.plane as any).position.y = arc;
        (this.plane as any).rotation.z = Math.sign(x) * Math.asin(effectiveX / R);
      }
    }

    this.speed = scroll.current - scroll.last;
    (this.program as any).uniforms.uTime.value += 0.04;
    (this.program as any).uniforms.uSpeed.value = this.speed;

    const planeOffset = (this.plane as any).scale.x / 2;
    const viewportOffset = this.viewport.width / 2;
    this.isBefore = (this.plane as any).position.x + planeOffset < -viewportOffset;
    this.isAfter = (this.plane as any).position.x - planeOffset > viewportOffset;
    if (direction === "right" && this.isBefore) {
      this.extra -= this.widthTotal;
      this.isBefore = this.isAfter = false;
    }
    if (direction === "left" && this.isAfter) {
      this.extra += this.widthTotal;
      this.isBefore = this.isAfter = false;
    }
  }
  onResize({ screen, viewport }: { screen?: { width: number; height: number }; viewport?: { width: number; height: number } } = {}) {
    if (screen) this.screen = screen;
    if (viewport) {
      this.viewport = viewport;
      if ((this.plane as any).program.uniforms.uViewportSizes) {
        (this.plane as any).program.uniforms.uViewportSizes.value = [this.viewport.width, this.viewport.height];
      }
    }
    this.scale = this.screen.height / 1500;
    (this.plane as any).scale.y = (this.viewport.height * (900 * this.scale)) / this.screen.height;
    (this.plane as any).scale.x = (this.viewport.width * (700 * this.scale)) / this.screen.width;
    (this.plane as any).program.uniforms.uPlaneSizes.value = [(this.plane as any).scale.x, (this.plane as any).scale.y];
    this.padding = 2;
    this.width = (this.plane as any).scale.x + this.padding;
    this.widthTotal = this.width * this.length;
    this.x = this.width * this.index;
  }
}

class App {
  container: HTMLDivElement;
  renderer!: Renderer & { gl: WebGLRenderingContext & { canvas: HTMLCanvasElement } };
  gl!: WebGLRenderingContext & { canvas: HTMLCanvasElement };
  camera!: Camera & { fov: number; position: { z: number }; perspective: (opts: { aspect: number }) => void };
  scene!: Transform;
  planeGeometry!: Plane;
  medias!: Media[];
  mediasImages!: Array<{ image: string; text: string }>;
  screen!: { width: number; height: number };
  viewport!: { width: number; height: number };
  scroll: { ease: number; current: number; target: number; last: number; position?: number };
  onCheckDebounce: () => void;
  bend!: number;
  raf: number = 0;
  isDown?: boolean;
  start?: number;
  scrollSpeed: number;

  constructor(
    container: HTMLDivElement,
    {
      items,
      bend,
      textColor = "#ffffff",
      borderRadius = 0,
      font = "bold 30px Figtree",
      scrollSpeed = 2,
      scrollEase = 0.05,
    }: any = {}
  ) {
    document.documentElement.classList.remove("no-js");
    this.container = container;
    this.scrollSpeed = scrollSpeed;
    this.scroll = { ease: scrollEase, current: 0, target: 0, last: 0 };
    this.onCheckDebounce = debounce(this.onCheck.bind(this), 200);
    this.createRenderer();
    this.createCamera();
    this.createScene();
    this.onResize();
    this.createGeometry();
    this.createMedias(items, bend, textColor, borderRadius, font);
    this.update();
    this.addEventListeners();
  }
  createRenderer() {
    this.renderer = new Renderer({
      alpha: true,
      antialias: true,
      dpr: Math.min((window.devicePixelRatio as number) || 1, 2),
    }) as any;
    this.gl = (this.renderer as any).gl;
    (this.gl as any).clearColor(0, 0, 0, 0);
    this.container.appendChild((this.gl as any).canvas);
  }
  createCamera() {
    this.camera = new Camera(this.gl as any) as any;
    (this.camera as any).fov = 45;
    (this.camera as any).position.z = 20;
  }
  createScene() {
    this.scene = new Transform();
  }
  createGeometry() {
    this.planeGeometry = new Plane(this.gl as any, {
      heightSegments: 50,
      widthSegments: 100,
    }) as any;
  }
  createMedias(items: Array<{ image: string; text: string }> | undefined, bend = 1, textColor: string, borderRadius: number, font: string) {
    const defaultItems = [
      { image: `https://picsum.photos/seed/1/800/600`, text: "Bridge" },
      { image: `https://picsum.photos/seed/2/800/600`, text: "Desk Setup" },
      { image: `https://picsum.photos/seed/3/800/600`, text: "Waterfall" },
      { image: `https://picsum.photos/seed/4/800/600`, text: "Strawberries" },
      { image: `https://picsum.photos/seed/5/800/600`, text: "Deep Diving" },
      { image: `https://picsum.photos/seed/16/800/600`, text: "Train Track" },
      { image: `https://picsum.photos/seed/17/800/600`, text: "Santorini" },
      { image: `https://picsum.photos/seed/8/800/600`, text: "Blurry Lights" },
      { image: `https://picsum.photos/seed/9/800/600`, text: "New York" },
      { image: `https://picsum.photos/seed/10/800/600`, text: "Good Boy" },
      { image: `https://picsum.photos/seed/21/800/600`, text: "Coastline" },
      { image: `https://picsum.photos/seed/12/800/600`, text: "Palm Trees" },
    ];
    const galleryItems = items && items.length ? items : defaultItems;
    this.mediasImages = galleryItems.concat(galleryItems);
    this.medias = this.mediasImages.map((data, index) => {
      return new Media({
        geometry: this.planeGeometry,
        gl: this.gl,
        image: data.image,
        index,
        length: this.mediasImages.length,
        renderer: this.renderer,
        scene: this.scene,
        screen: this.screen,
        text: data.text,
        viewport: this.viewport,
        bend,
        textColor,
        borderRadius,
        font,
      });
    });
  }
  onTouchDown = (e: MouseEvent | TouchEvent) => {
    this.isDown = true;
    this.scroll.position = this.scroll.current;
    const touch = (e as TouchEvent).touches ? (e as TouchEvent).touches[0] : (e as MouseEvent);
    this.start = (touch as any).clientX;
  };
  onTouchMove = (e: MouseEvent | TouchEvent) => {
    if (!this.isDown) return;
    const touch = (e as TouchEvent).touches ? (e as TouchEvent).touches[0] : (e as MouseEvent);
    const x = (touch as any).clientX;
    const distance = (this.start! - x) * (this.scrollSpeed * 0.025);
    this.scroll.target = this.scroll.position! + distance;
  };
  onTouchUp = () => {
    this.isDown = false;
    this.onCheck();
  };
  onWheel = (e: any) => {
    const delta = e.deltaY || e.wheelDelta || e.detail;
    this.scroll.target += (delta > 0 ? this.scrollSpeed : -this.scrollSpeed) * 0.2;
    this.onCheckDebounce();
  };
  onCheck = () => {
    if (!this.medias || !this.medias[0]) return;
    const width = this.medias[0].width;
    const itemIndex = Math.round(Math.abs(this.scroll.target) / width);
    const item = width * itemIndex;
    this.scroll.target = this.scroll.target < 0 ? -item : item;
  };
  onResize = () => {
    this.screen = {
      width: this.container.clientWidth,
      height: this.container.clientHeight,
    };
    this.renderer.setSize(this.screen.width, this.screen.height);
    (this.camera as any).perspective({
      aspect: this.screen.width / this.screen.height,
    });
    const fov = (((this.camera as any).fov as number) * Math.PI) / 180;
    const height = 2 * Math.tan(fov / 2) * (this.camera as any).position.z;
    const width = height * (this.camera as any).aspect;
    this.viewport = { width, height } as any;
    if (this.medias) {
      this.medias.forEach((media) => media.onResize({ screen: this.screen, viewport: this.viewport }));
    }
  };
  update = () => {
    this.scroll.current = lerp(this.scroll.current, this.scroll.target, this.scroll.ease);
    const direction: "left" | "right" = this.scroll.current > this.scroll.last ? "right" : "left";
    if (this.medias) {
      this.medias.forEach((media) => media.update(this.scroll, direction));
    }
    this.renderer.render({ scene: this.scene, camera: this.camera as any });
    this.scroll.last = this.scroll.current;
    this.raf = window.requestAnimationFrame(this.update);
  };
  addEventListeners() {
    window.addEventListener("resize", this.onResize);
    window.addEventListener("mousewheel", this.onWheel as any);
    window.addEventListener("wheel", this.onWheel as any);
    window.addEventListener("mousedown", this.onTouchDown as any);
    window.addEventListener("mousemove", this.onTouchMove as any);
    window.addEventListener("mouseup", this.onTouchUp as any);
    window.addEventListener("touchstart", this.onTouchDown as any);
    window.addEventListener("touchmove", this.onTouchMove as any);
    window.addEventListener("touchend", this.onTouchUp as any);
  }
  destroy() {
    window.cancelAnimationFrame(this.raf);
    window.removeEventListener("resize", this.onResize);
    window.removeEventListener("mousewheel", this.onWheel as any);
    window.removeEventListener("wheel", this.onWheel as any);
    window.removeEventListener("mousedown", this.onTouchDown as any);
    window.removeEventListener("mousemove", this.onTouchMove as any);
    window.removeEventListener("mouseup", this.onTouchUp as any);
    window.removeEventListener("touchstart", this.onTouchDown as any);
    window.removeEventListener("touchmove", this.onTouchMove as any);
    window.removeEventListener("touchend", this.onTouchUp as any);
    if ((this.renderer as any) && (this.renderer as any).gl && (this.renderer as any).gl.canvas.parentNode) {
      (this.renderer as any).gl.canvas.parentNode.removeChild((this.renderer as any).gl.canvas);
    }
  }
}

export type CircularGalleryItem = { image: string; text: string };

export default function CircularGallery({
  items,
  bend = 3,
  textColor = "#ffffff",
  borderRadius = 0.05,
  font = "bold 30px Figtree",
  scrollSpeed = 2,
  scrollEase = 0.05,
}: {
  items?: CircularGalleryItem[];
  bend?: number;
  textColor?: string;
  borderRadius?: number;
  font?: string;
  scrollSpeed?: number;
  scrollEase?: number;
}) {
  const containerRef = useRef<HTMLDivElement | null>(null);
  useEffect(() => {
    if (!containerRef.current) return;
    const app = new App(containerRef.current, { items, bend, textColor, borderRadius, font, scrollSpeed, scrollEase });
    return () => {
      app.destroy();
    };
  }, [items, bend, textColor, borderRadius, font, scrollSpeed, scrollEase]);
  return <div className={styles.circularGallery} ref={containerRef} />;
}
